<template>
  <div class="block-renderer" :class="{ 'block-renderer--readonly': block.readOnly }">
    <component
      :is="blockComponent"
      :block="block"
      :page-id="pageId"
      :limits="limits"
      @update="$emit('update', $event)"
      @delete="$emit('delete')"
    />

    <!-- ReadOnly overlay for premium blocks -->
    <div v-if="block.readOnly" class="block-renderer__overlay" @click="goToPlans">
      <div class="block-renderer__overlay-content">
        <v-icon icon="mdi-lock" size="24" class="block-renderer__lock-icon" />
        <div class="block-renderer__overlay-text">
          <strong>Premium </strong>
          <p>   </p>
        </div>
        <button class="block-renderer__upgrade-btn" @click.stop="goToPlans">
          
          <v-icon icon="mdi-arrow-right" size="14" />
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import TextBlock from './TextBlock.vue'
import HeadingBlock from './HeadingBlock.vue'
import DividerBlock from './DividerBlock.vue'
import ChecklistBlock from './ChecklistBlock.vue'
import BulletListBlock from './BulletListBlock.vue'
import KeyValueBlock from './KeyValueBlock.vue'
import IdeaCardBlock from './IdeaCardBlock.vue'
import ProsBlock from './ProsBlock.vue'
import ConsBlock from './ConsBlock.vue'
import LinkBlock from './LinkBlock.vue'
import StatusBlock from './StatusBlock.vue'
import QuoteBlock from './QuoteBlock.vue'

const blockComponents = {
  TEXT: TextBlock,
  HEADING: HeadingBlock,
  DIVIDER: DividerBlock,
  CHECKLIST: ChecklistBlock,
  BULLET_LIST: BulletListBlock,
  KEY_VALUE: KeyValueBlock,
  IDEA_CARD: IdeaCardBlock,
  PROS: ProsBlock,
  CONS: ConsBlock,
  LINK: LinkBlock,
  STATUS: StatusBlock,
  QUOTE: QuoteBlock,
}

export default {
  name: 'BlockRenderer',
  props: {
    block: {
      type: Object,
      required: true,
    },
    pageId: {
      type: String,
      required: true,
    },
    limits: {
      type: Object,
      default: null,
    },
  },
  emits: ['update', 'delete'],
  computed: {
    blockComponent() {
      // API   type  blockType
      const blockType = this.block.type || this.block.blockType
      return blockComponents[blockType] || TextBlock
    },
  },
  methods: {
    goToPlans() {
      this.$router.push({ name: 'subscription-plans' })
    },
  },
}
</script>

<style scoped>
.block-renderer {
  position: relative;
}

.block-renderer--readonly {
  opacity: 0.7;
}

.block-renderer__overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(13, 13, 26, 0.85);
  backdrop-filter: blur(2px);
  border-radius: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  cursor: pointer;
}

.block-renderer__overlay-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  padding: 20px;
  text-align: center;
}

.block-renderer__lock-icon {
  color: #9370db;
}

.block-renderer__overlay-text strong {
  display: block;
  font-size: 15px;
  font-weight: 600;
  color: #fff;
  margin-bottom: 4px;
}

.block-renderer__overlay-text p {
  margin: 0;
  font-size: 13px;
  color: #a8a8b8;
}

.block-renderer__upgrade-btn {
  display: flex;
  align-items: center;
  gap: 6px;
  margin-top: 8px;
  padding: 8px 16px;
  border-radius: 8px;
  background: linear-gradient(135deg, #9370db 0%, #6a5acd 100%);
  border: none;
  color: #fff;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.block-renderer__upgrade-btn:active {
  transform: scale(0.95);
  opacity: 0.9;
}
</style>
