<template>
  <div class="page-detail">
    <!-- Header -->
    <div class="page-detail__header">
      <button class="page-detail__back" @click="goBack">
        <v-icon icon="mdi-arrow-left" size="24" />
      </button>
      <div class="page-detail__title-wrapper">
        <input
          v-if="editingTitle"
          ref="titleInput"
          v-model="titleDraft"
          type="text"
          class="page-detail__title-input"
          maxlength="200"
          @blur="saveTitle"
          @keyup.enter="saveTitle"
          @keyup.escape="cancelTitleEdit"
        />
        <h1
          v-else
          class="page-detail__title"
          @click="startTitleEdit"
        >
          {{ page?.title || '...' }}
        </h1>
      </div>
      <button class="page-detail__delete" @click="showDeleteConfirm = true">
        <v-icon icon="mdi-delete-outline" size="22" />
      </button>
    </div>

    <!-- Structure Locked Banner -->
    <div
      v-if="page?.blocks?.length > 10 && limits && !limits.canDeleteBlocks"
      class="page-detail__locked-banner"
    >
      <div class="page-detail__locked-content">
        <v-icon icon="mdi-lock" size="20" class="page-detail__locked-icon" />
        <div class="page-detail__locked-text">
          <strong>  </strong>
          <p>   ,   / </p>
        </div>
      </div>
      <button class="page-detail__locked-cta" @click="goToPlans">
         Premium
        <v-icon icon="mdi-arrow-right" size="16" />
      </button>
    </div>

    <!-- Content -->
    <div class="page-detail__content">
      <!-- Loading -->
      <div v-if="loading" class="page-detail__loading">
        <div class="page-detail__loading-block" v-for="i in 3" :key="i"></div>
      </div>

      <!-- Empty state -->
      <EmptyState
        v-else-if="!page?.blocks?.length"
        icon="mdi-text-box-plus-outline"
        title=" "
        subtitle="    "
        action-text=" "
        @action="handleFabClick"
      />

      <!-- Blocks -->
      <transition-group v-else name="fade-up" tag="div" class="page-detail__blocks">
        <BlockRenderer
          v-for="(block, index) in page.blocks"
          :key="block.id"
          :block="block"
          :page-id="pageId"
          :limits="limits"
          :style="{ transitionDelay: index * 0.03 + 's' }"
          @update="updateBlock(index, $event)"
          @delete="deleteBlock(block, index)"
        />
      </transition-group>
    </div>

    <!-- FAB -->
    <button
      class="page-detail__fab"
      @click="handleFabClick"
    >
      <v-icon icon="mdi-plus" size="28" />
    </button>

    <!-- Block Selector -->
    <BlockTypeSelector
      :show="showBlockSelector"
      :block-types="blockTypes"
      :is-premium="isPremium"
      :limits="limits"
      @close="showBlockSelector = false"
      @select="createBlock"
    />

    <!-- Delete Confirm -->
    <ConfirmDialog
      :show="showDeleteConfirm"
      title=" ?"
      message="       ."
      confirm-text=""
      :danger="true"
      @confirm="deletePage"
      @cancel="showDeleteConfirm = false"
    />

    <!-- Block Delete Confirm -->
    <ConfirmDialog
      :show="showBlockDeleteConfirm"
      title=" ?"
      message="   ."
      confirm-text=""
      :danger="true"
      @confirm="confirmDeleteBlock"
      @cancel="cancelDeleteBlock"
    />

    <!-- Toast Notification -->
    <v-snackbar
      v-model="showToast"
      :color="toastColor"
      :timeout="5000"
      location="bottom"
    >
      {{ toastMessage }}

      <template v-slot:actions>
        <v-btn
          v-if="toastAction"
          variant="text"
          @click="toastAction.onClick(); showToast = false"
        >
          {{ toastAction.text }}
        </v-btn>
        <v-btn
          icon="mdi-close"
          variant="text"
          size="small"
          @click="showToast = false"
        />
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import BlockRenderer from '@/components/blocks/BlockRenderer.vue'
import BlockTypeSelector from '@/components/blocks/BlockTypeSelector.vue'
import EmptyState from '@/components/common/EmptyState.vue'
import ConfirmDialog from '@/components/common/ConfirmDialog.vue'
import { PagesCall, BlocksCall, SubscriptionCall } from '@/api/call'

const PREMIUM_BLOCK_TYPES = [
  'HEADING', 'BULLET_LIST', 'KEY_VALUE', 'IDEA_CARD',
  'LINK', 'STATUS', 'QUOTE', 'PROS', 'CONS',
]

export default {
  name: 'PageDetailView',
  components: {
    BlockRenderer,
    BlockTypeSelector,
    EmptyState,
    ConfirmDialog,
  },
  data() {
    return {
      loading: true,
      page: null,
      editingTitle: false,
      titleDraft: '',
      showBlockSelector: false,
      showDeleteConfirm: false,
      showBlockDeleteConfirm: false,
      blockToDelete: null,
      blockIndexToDelete: null,
      blockTypes: [],
      isPremium: false,
      limits: null,
      showToast: false,
      toastMessage: '',
      toastAction: null,
      toastColor: 'error',
    }
  },
  computed: {
    // isLimitReached removed as we rely on backend validation
    pageId() {
      return this.$route.params.id
    },
  },
  async mounted() {
    await this.loadData()
  },
  methods: {
    async loadData() {
      try {
        this.loading = true
        const [pageResponse, typesResponse, limitsResponse] = await Promise.all([
          PagesCall.getPage(this.pageId),
          PagesCall.getBlockTypes(),
          SubscriptionCall.getLimits(),
        ])
        this.page = pageResponse.data
        this.blockTypes = typesResponse.data.blocks || []
        this.limits = limitsResponse.data
        this.isPremium = this.limits.plan === 'PREMIUM'
      } catch (error) {
        console.error('Failed to load page:', error)
        this.$router.replace({ name: 'pages' })
      } finally {
        this.loading = false
      }
    },
    async refreshLimits() {
      try {
        const response = await SubscriptionCall.getLimits()
        this.limits = response.data
        this.isPremium = this.limits.plan === 'PREMIUM'
      } catch (error) {
        console.error('Failed to refresh limits:', error)
      }
    },
    goBack() {
      this.$router.push({ name: 'pages' })
    },
    goToPlans() {
      this.$router.push({ name: 'subscription-plans' })
    },
    startTitleEdit() {
      this.titleDraft = this.page?.title || ''
      this.editingTitle = true
      this.$nextTick(() => {
        this.$refs.titleInput?.focus()
        this.$refs.titleInput?.select()
      })
    },
    async saveTitle() {
      if (!this.titleDraft.trim()) {
        this.titleDraft = this.page?.title || ''
      }

      if (this.titleDraft === this.page?.title) {
        this.editingTitle = false
        return
      }

      const scrollX = window.scrollX
      const scrollY = window.scrollY

      try {
        await PagesCall.updatePage(this.pageId, { title: this.titleDraft.trim() })
        this.page.title = this.titleDraft.trim()
      } catch (error) {
        console.error('Failed to update title:', error)
      }

      this.editingTitle = false

      this.$nextTick(() => {
        window.scrollTo(scrollX, scrollY)
      })
    },
    cancelTitleEdit() {
      this.editingTitle = false
    },
    handleFabClick() {
      this.showBlockSelector = true
    },
    async createBlock(blockType) {
      this.showBlockSelector = false

      if (PREMIUM_BLOCK_TYPES.includes(blockType) && this.limits && !this.limits.canEditPremiumBlocks) {
        this.displayToast({
          message: '     Premium .',
          color: 'info',
          action: { text: ' ', onClick: () => this.goToPlans() },
        })
        return
      }

      const payload = this.getDefaultBlockPayload(blockType)

      try {
        const response = await BlocksCall.createBlock(this.pageId, payload)
        this.page.blocks.push(response.data)
        await this.refreshLimits()
      } catch (error) {
        console.error('Failed to create block:', error)
        this.handle403Error(error)
      }
    },
    getDefaultBlockPayload(type) {
      switch (type) {
        case 'TEXT':
        case 'PROS':
        case 'CONS':
          return { type, textContent: '' }
        case 'HEADING':
          return { type, header: '', level: 1 }
        case 'DIVIDER':
          return { type }
        case 'CHECKLIST':
          return { type, items: [{ text: '', status: false }] }
        case 'BULLET_LIST':
          return { type, items: [''] }
        case 'KEY_VALUE':
          return { type, items: [{ key: '', value: '' }] }
        case 'IDEA_CARD':
          return { type, problem: '', solution: '', audience: '', mvp: '', monetization: '' }
        case 'LINK':
          return { type, url: '', text: '' }
        case 'STATUS':
          return { type, status: 'IDEA' }
        case 'QUOTE':
          return { type, text: '', author: '' }
        default:
          return { type }
      }
    },
    updateBlock(index, updatedBlock) {
      this.page.blocks[index] = updatedBlock
    },
    deleteBlock(block, index) {
      if (this.limits && !this.limits.canDeleteBlocks) {
        this.displayToast({
          message: '  .  Premium   .',
          color: 'warning',
          action: { text: '', onClick: () => this.goToPlans() },
        })
        return
      }

      this.blockToDelete = block
      this.blockIndexToDelete = index
      this.showBlockDeleteConfirm = true
    },
    async confirmDeleteBlock() {
      if (!this.blockToDelete) return

      try {
        await BlocksCall.deleteBlock(this.pageId, this.blockToDelete.id)
        this.page.blocks.splice(this.blockIndexToDelete, 1)
        await this.refreshLimits()
      } catch (error) {
        console.error('Failed to delete block:', error)
        this.handle403Error(error)
      }

      this.blockToDelete = null
      this.blockIndexToDelete = null
      this.showBlockDeleteConfirm = false
    },
    cancelDeleteBlock() {
      this.blockToDelete = null
      this.blockIndexToDelete = null
      this.showBlockDeleteConfirm = false
    },
    async deletePage() {
      try {
        await PagesCall.deletePage(this.pageId)
        this.$router.replace({ name: 'pages' })
      } catch (error) {
        console.error('Failed to delete page:', error)
      }
      this.showDeleteConfirm = false
    },
    displayToast({ message, color = 'error', action = null }) {
      this.toastMessage = message
      this.toastColor = color
      this.toastAction = action
      this.showToast = true
    },
    handle403Error(error) {
      if (error.response?.status !== 403) return

      const reason = error.reason || error.response?.data?.reason

      switch (reason) {
        case 'STRUCTURE_LOCKED':
          this.displayToast({
            message: ' .  Premium  .',
            color: 'warning',
            action: { text: '', onClick: () => this.goToPlans() },
          })
          break
        case 'PREMIUM_BLOCK_REQUIRED':
          this.displayToast({
            message: '    Premium .',
            color: 'info',
            action: { text: ' ', onClick: () => this.goToPlans() },
          })
          break
        case 'PREMIUM_BLOCK_READ_ONLY':
          this.displayToast({
            message: '   Premium.',
            color: 'info',
            action: { text: '', onClick: () => this.goToPlans() },
          })
          break
        default:
          //    ,   
          this.displayToast({ 
            message: error.response?.data?.message || ' ',
            color: 'warning'
          })
      }
    },
  },
}
</script>

<style scoped>
.page-detail {
  min-height: 100vh;
  background: #0d0d1a;
  padding-bottom: 100px;
}

.page-detail__header {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px;
  position: sticky;
  top: 0;
  background: #0d0d1a;
  z-index: 50;
  border-bottom: 1px solid rgba(255, 255, 255, 0.06);
}

.page-detail__back {
  width: 44px;
  height: 44px;
  border-radius: 14px;
  background: rgba(255, 255, 255, 0.08);
  border: none;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  flex-shrink: 0;
}

.page-detail__back:active {
  transform: scale(0.95);
}

.page-detail__title-wrapper {
  flex: 1;
  min-width: 0;
}

.page-detail__title {
  font-size: 20px;
  font-weight: 600;
  color: #fff;
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  padding: 8px 0;
}

.page-detail__title-input {
  width: 100%;
  padding: 8px 12px;
  border-radius: 10px;
  border: 1px solid rgba(147, 112, 219, 0.5);
  background: rgba(255, 255, 255, 0.05);
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  outline: none;
}

.page-detail__delete {
  width: 44px;
  height: 44px;
  border-radius: 14px;
  background: rgba(255, 255, 255, 0.08);
  border: none;
  color: #a8a8b8;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  flex-shrink: 0;
}

.page-detail__delete:active {
  transform: scale(0.95);
}

.page-detail__delete:hover {
  color: #ef4444;
  background: rgba(239, 68, 68, 0.1);
}

/* Structure Locked Banner */
.page-detail__locked-banner {
  margin: 0 16px;
  padding: 14px 16px;
  border-radius: 12px;
  background: linear-gradient(135deg, rgba(245, 158, 11, 0.1) 0%, rgba(234, 88, 12, 0.1) 100%);
  border: 1px solid rgba(245, 158, 11, 0.3);
  display: flex;
  align-items: center;
  gap: 12px;
}

.page-detail__locked-content {
  flex: 1;
  display: flex;
  align-items: flex-start;
  gap: 10px;
}

.page-detail__locked-icon {
  color: #f59e0b;
  flex-shrink: 0;
  margin-top: 2px;
}

.page-detail__locked-text {
  flex: 1;
}

.page-detail__locked-text strong {
  display: block;
  font-size: 14px;
  font-weight: 600;
  color: #f59e0b;
  margin-bottom: 4px;
}

.page-detail__locked-text p {
  margin: 0;
  font-size: 13px;
  color: #d1d1db;
  line-height: 1.4;
}

.page-detail__locked-cta {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 14px;
  border-radius: 8px;
  background: linear-gradient(135deg, #9370db 0%, #6a5acd 100%);
  border: none;
  color: #fff;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  white-space: nowrap;
  flex-shrink: 0;
}

.page-detail__locked-cta:active {
  transform: scale(0.95);
  opacity: 0.9;
}

.page-detail__content {
  padding: 16px;
}

.page-detail__blocks {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

/* Loading */
.page-detail__loading {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.page-detail__loading-block {
  height: 80px;
  border-radius: 14px;
  background: linear-gradient(145deg, #1a1a2e 0%, #16213e 100%);
  border: 1px solid rgba(255, 255, 255, 0.08);
  animation: pulse 1.8s ease-in-out infinite;
}

.page-detail__loading-block:nth-child(2) {
  animation-delay: 0.15s;
}

.page-detail__loading-block:nth-child(3) {
  animation-delay: 0.3s;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.4; }
}

/* FAB */
.page-detail__fab {
  position: fixed;
  bottom: 24px;
  right: 16px;
  width: 60px;
  height: 60px;
  border-radius: 18px;
  background: linear-gradient(135deg, #9370db 0%, #6a5acd 100%);
  border: none;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 8px 24px rgba(147, 112, 219, 0.4);
  transition: all 0.2s ease;
  z-index: 100;
}

.page-detail__fab:active {
  transform: scale(0.95);
}

.page-detail__fab--disabled {
  opacity: 0.6;
  background: linear-gradient(135deg, #5a5a6e 0%, #4a4a5e 100%);
  box-shadow: 0 4px 12px rgba(90, 90, 110, 0.3);
}

.page-detail__fab--disabled:active {
  transform: none;
}

/* Animations */
.fade-up-enter-active {
  transition: all 0.4s cubic-bezier(0.16, 1, 0.3, 1);
}

.fade-up-enter-from {
  opacity: 0;
  transform: translateY(16px);
}
</style>
